import { Jotai_jotaiUtils } from "../Bindings/Jotai.fs.js";
import { Atoms_cellSize, Atoms_Task_priority } from "../State.fs.js";
import { map, defaultArg } from "../../.fable/fable-library.3.2.7/Option.js";
import { int32ToString } from "../../.fable/fable-library.3.2.7/Util.js";
import { Priority_toTag } from "../../../Fluke.Shared/Domain/ModelGenerated.fs.js";
import { box } from "../Bindings/Chakra.fs.js";
import { interpolate, toText } from "../../.fable/fable-library.3.2.7/String.js";

export function TaskPriority(taskPriorityInputProps) {
    const priority = Jotai_jotaiUtils.useAtomValue(Atoms_Task_priority(taskPriorityInputProps.taskId));
    const cellSize = Jotai_jotaiUtils.useAtomValue(Atoms_cellSize) | 0;
    const priorityText = defaultArg(map((arg_1) => int32ToString(1 + Priority_toTag(arg_1)), priority), "");
    return box()((x_2) => {
        x_2.position = "relative";
        x_2.height = toText(interpolate("%P()px", [cellSize]));
        x_2.lineHeight = toText(interpolate("%P()px", [cellSize]));
    })([priorityText]);
}

