import react from "fast-deep-equal/react";
import { compare as compare_1, equals } from "../../.fable/fable-library.3.2.7/Util.js";

const fastDeepEqual = react;

export function compare(a, b) {
    const matchValue = [a, b];
    if ((!equals(matchValue[0], null)) ? (!equals(matchValue[1], null)) : false) {
        return compare_1(matchValue[0], matchValue[1]) === 0;
    }
    else {
        return fastDeepEqual(a, b);
    }
}

