import { getTicks, now } from "../../.fable/fable-library.3.2.7/Date.js";
import { set$ } from "./Dom.fs.js";
import { JS_isDebug } from "./JS.fs.js";
import { getItemFromDict } from "../../.fable/fable-library.3.2.7/MapUtil.js";
import { DateTime_ticksDiff } from "../../../Fluke.Shared/Core.fs.js";

const initialTicks = (() => {
    let copyOfStruct = now();
    return getTicks(copyOfStruct);
})();

export const profilingState = {
    CallCount: new Map([]),
    Timestamps: [],
};

set$("profilingState", profilingState);

export function addCount(id) {
    if (JS_isDebug()) {
        const matchValue = profilingState.CallCount.has(id);
        if (matchValue) {
            profilingState.CallCount.set(id, getItemFromDict(profilingState.CallCount, id) + 1);
        }
        else {
            profilingState.CallCount.set(id, 1);
        }
    }
}

export function addTimestamp(id) {
    if (JS_isDebug()) {
        void (profilingState.Timestamps.push([id, DateTime_ticksDiff(initialTicks)]));
    }
}

addTimestamp("Init");

