import jssha_1 from "jssha";
import { addToDict, getItemFromDict } from "../../.fable/fable-library.3.2.7/MapUtil.js";
import { parse } from "../../.fable/fable-library.3.2.7/Guid.js";

const jssha = jssha_1;

function shake128(str) {
    const hash = new jssha("SHAKE128", "TEXT");
    hash.update(str);
    return hash.getHash("HEX", {
        outputLen: 128,
    });
}

const hashCache = new Map([]);

export function getTextGuidHash(value) {
    if (hashCache.has(value)) {
        return getItemFromDict(hashCache, value);
    }
    else {
        const hash = parse(shake128(value));
        addToDict(hashCache, value, hash);
        return hash;
    }
}

