import { Jotai_jotaiUtils } from "../Bindings/Jotai.fs.js";
import { Atoms_darkMode } from "../State.fs.js";
import { singleton, delay, toList } from "../../.fable/fable-library.3.2.7/Seq.js";
import { checkbox, box } from "../Bindings/Chakra.fs.js";
import { Helpers_nothing } from "../../.fable/Fable.React.7.4.1/Fable.React.Helpers.fs.js";

export function Checkbox(checkboxInputProps) {
    const props = checkboxInputProps.props;
    const label = checkboxInputProps.label;
    const darkMode = Jotai_jotaiUtils.useAtomValue(Atoms_darkMode);
    const arg10 = toList(delay(() => {
        if (label != null) {
            const label_1 = label;
            return singleton(box()((x_1) => {
                x_1.fontSize = "main";
            })([label_1]));
        }
        else {
            return singleton(Helpers_nothing);
        }
    }));
    return checkbox()((x) => {
        x.colorScheme = "purple";
        x.borderColor = (darkMode ? "#484848" : "#b7b7b7");
        x.size = "lg";
        props(x);
    })(arg10);
}

